body {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: Roboto, Arial, sans-serif;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

.header {
  box-sizing: border-box;
  background-color: #fff;
  justify-content: space-between;
  align-items: baseline;
  display: flex;
  box-shadow: 0 2px 4px #0000000d;
}

.header__logo-link {
  width: 40px;
  height: 40px;
  margin-top: auto;
  margin-bottom: auto;
  margin-left: 50px;
  display: inline-flex;
}

.header__nav {
  margin-right: 50px;
}

.header__list {
  align-items: center;
  height: 60px;
  display: flex;
}

.header__item {
  margin-top: auto;
  margin-bottom: auto;
}

.header__item:nth-child(-n+7) {
  margin-right: 20px;
}

.header__link {
  text-align: center;
  text-transform: uppercase;
  color: #060b35;
  justify-content: center;
  align-items: center;
  height: 60px;
  margin: 0;
  padding: 0;
  font-size: 12px;
  font-weight: 500;
  line-height: 14px;
  text-decoration: none;
  transition-duration: .3s;
  display: flex;
}

.header__link:hover {
  color: #00acdc;
}

.is-active {
  color: #00acdc;
  position: relative;
}

.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
}

.card {
  box-sizing: border-box;
  background-color: #fff;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  width: 200px;
  height: 408px;
  margin: 0;
  padding: 32px 16px 16px;
  transition: transform .3s;
}

.card__image {
  justify-content: center;
  margin-bottom: 40px;
  display: flex;
}

.card .image {
  width: 160px;
  height: 134px;
}

.card__title {
  color: #060b35;
  margin-top: 0;
  margin-bottom: 4px;
  font-size: 12px;
  font-weight: 500;
  line-height: 150%;
}

.card__code {
  color: #616070;
  margin-top: 0;
  margin-bottom: 16px;
  font-size: 10px;
  font-weight: 400;
  line-height: 140%;
}

.card__stars {
  margin-bottom: 24px;
}

.card__price {
  box-sizing: border-box;
  justify-content: space-between;
  margin-bottom: 16px;
  line-height: 18px;
  display: flex;
}

.card__price-title {
  color: #616070;
  margin: 0;
  font-size: 12px;
  font-weight: 400;
  line-height: 18px;
}

.card__price-number {
  color: #060b35;
  margin: 0;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__button {
  box-sizing: border-box;
  text-transform: uppercase;
  color: #fff;
  background-color: #00acdc;
  border: none;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 40px;
  font-size: 14px;
  font-weight: 700;
  line-height: 114%;
  text-decoration: none;
  transition-duration: .3s;
  display: flex;
}

.card__button:hover {
  color: #00acdc;
  background-color: #fff;
  border: 1px solid #00acdc;
  border-radius: 5px;
}

.card:hover {
  transform: scale(1.2);
}

.card:hover .card__title {
  color: #34568b;
}

.stars {
  box-sizing: border-box;
  justify-content: center;
  align-items: center;
  display: flex;
}

.stars__item {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
}

.stars--1 .stars__item:nth-child(-n+1), .stars--2 .stars__item:nth-child(-n+2), .stars--3 .stars__item:nth-child(-n+3), .stars--4 .stars__item:nth-child(-n+4), .stars--5 .stars__item:nth-child(-n+5) {
  background-image: url("star-active.edda4c43.svg");
}

.stars :nth-child(-n+4) {
  margin-right: 4px;
}

.stars__review {
  margin: 0 0 0 auto;
  font-size: 10px;
  font-weight: 400;
  line-height: 140%;
}

.catalog {
  box-sizing: border-box;
  grid-template-columns: repeat(1, 200px);
  justify-content: center;
  gap: 46px 48px;
  padding: 50px 40px;
  display: grid;
}

@media (width >= 488px) {
  .catalog {
    grid-template-columns: repeat(2, 200px);
  }
}

@media (width >= 768px) {
  .catalog {
    grid-template-columns: repeat(3, 200px);
  }
}

@media (width >= 1024px) {
  .catalog {
    grid-template-columns: repeat(4, 200px);
  }
}
